import { Component, OnInit } from '@angular/core';

import { RestService } from 'app/core_auth/services/rest.service';

@Component({
  selector: 'ngx-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})
export class CategoryListComponent implements OnInit {
  category: any;
    
  constructor(private restService: RestService) { }

  categories: any [];

  ngOnInit() {

    
    this.reloadData();
  }
  reloadData() {
    this.restService.get("/category/categoryList").subscribe((data) => {
      this.categories = data.data;
    }, (error) => {
      console.log(error)
    });
  }

  deleteCategoty(id: string) {
    const category = this.categories.find(x => x.i === id);
    category.isDeleting = true;
    this.restService.delete(id)
        .subscribe(() => {
            this.categories = this.categories.filter(x => x.i !== id) 
        });
  }


}
