import { Component, OnInit } from '@angular/core';
import { RestService } from 'app/core_auth/services/rest.service';

@Component({
  selector: 'ngx-tours-list',
  templateUrl: './tours-list.component.html',
  styleUrls: ['./tours-list.component.scss']
})
export class ToursListComponent implements OnInit {

  tour: any;
    
  constructor(private restService: RestService) { }

  tours: any [];

  ngOnInit(){
    this.reloadData();
  }

  reloadData() {
    this.restService.get("/tours/list").subscribe((data) => {
      this.tours = data.data;
    }, (error) => {
      console.log(error)
    });
  }

  deleteTours (id: string) {
    const tour = this.tours.find(x => x.i === id);
    tour.isDeleting = true;
    this.restService.delete(id)
        .subscribe(() => {
            this.tours = this.tours.filter(x => x.i !== id) 
        });
  }

}
