import { Component } from '@angular/core';

@Component({
  selector: 'ngx-badge',
  template: `<nav class="navbar navbar-expand-sm bg-primary navbar-dark">
  <!-- Links -->
  <ul class="navbar-nav">
    <li class="nav-item">
        <a routerLink="badge-list" class="nav-link" routerLinkActive="active">Badge List</a>
    </li>
    <li class="nav-item">
        <a routerLink="add" class="nav-link" routerLinkActive="active">Add Badge</a>
    </li>
  </ul>
</nav>


<div class="container">
  <br>
  
  <div class="card">
    <div class="card-body">
      <router-outlet></router-outlet>
    </div>
  </div>
</div>`,
})
export class BadgeComponent {

    title = 'Angular 9 + Spring Boot 2 CRUD Tutorial';
}
